import React from "react";
import { Text } from "common";

export const TextInput = (props) => {
  return (
    <input
      type={props.type}
      name={props.name}
      className={props.className + " form-control"}
      style={{ ...props.style }}
      id={props.id}
      value={props.value && props.value}
      placeholder={props.placeholder}
    />
  );
};

export const TextArea = (props) => {
  return (
    <textarea
      name={props.name}
      className={props.className + " form-control"}
      style={{ ...props.style }}
      id={props.id}
      cols={props.cols ? props.cols : 30}
      rows={props.cols ? props.rows : 4}
      placeholder={props.placeholder}
    >
      {props.value && props.value}
    </textarea>
  );
};

export const Dropdown = (props) => {
  return (
    <select
      className={props.className}
      style={{ ...props.style }}
      autoFocus={false}
    >
      <option
        className={props.optionClass}
        style={{ ...props.optionStyle }}
        value={0}
      >
        {props.placeholder ? props.placeholder : "--SELECT OPTION--"}
      </option>
      {props.items &&
        props.items.length > 0 &&
        props.items.map((item, idx) => {
          return (
            <option
              key={idx}
              className={props.optionClass}
              style={{ ...props.optionStyle }}
              value={item.value}
            >
              {item.label}
            </option>
          );
        })}
    </select>
  );
};

export const Radio = (props) => {
  return (
    <React.Fragment>
      {props.items.map((item, idx) => {
        let checked = props.checked == item.value ? true : false;
        return (
          <div
            className={props.className}
            key={idx}
            onClick={() => props.onChange(item.value)}
          >
            <input
              type="radio"
              className={props.radioClass}
              checked={checked}
            />
            {typeof item.label == "string" ? (
              <Text className={props.labelClass}>{item.label}</Text>
            ) : (
              item.label
            )}
          </div>
        );
      })}
    </React.Fragment>
  );
};
