import React from "react";
import { Link, Text } from "../../common";

export const ActionButton = (props) => {
  return (
    <React.Fragment>
      {props.items.map((item, idx) => {
        return (
          <Link onPress={item.onPress} key={`element${idx}`}>
            <Text className={item.icon}></Text>
          </Link>
        );
      })}
    </React.Fragment>
  );
};
