import React from "react";

export const THead = (props) => {
  return (
    <thead className="thead-light">
      <tr>
        {props.items &&
          props.items.length > 0 &&
          props.items.map((heading, idx) => {
            return (
              <th
                key={idx}
                className={props.headClass}
                style={{ ...props.headStyle }}
              >
                {heading}
              </th>
            );
          })}
      </tr>
    </thead>
  );
};

export const TBody = (props) => {
  return (
    <tbody>
      {props.items &&
        props.items.length > 0 &&
        props.items.map((rows, idx) => {
          return (
            <tr
              key={idx}
              className={props.bodyClass}
              style={{ ...props.bodyStyle }}
            >
              {Object.keys(rows).map(function (key, index) {
                return (
                  <td
                    id={index}
                    style={{ textAlign: index == 0 ? "left" : "" }}
                  >
                    {rows[key]}
                  </td>
                );
              })}
            </tr>
          );
        })}
    </tbody>
  );
};

export default function Table(props) {
  return (
    <table className={props.className} style={{ ...props.style }}>
      <THead
        items={props.headings}
        headClass={props.headClass}
        headStyle={props.headStyle}
      />
      <TBody
        items={props.data}
        bodyClass={props.bodyClass}
        bodyStyle={props.bodyStyle}
      />
    </table>
  );
}
