import React from "react";
import { Link, Text } from "common";
import classNames from "classnames";

function ProfileMenu({ items }) {
  return (
    <React.Fragment>
      {items.map(({ label, link, active }, idx) => {
        let classess = classNames({
          [`dropdown-item`]: true,
          active: active ? true : false,
        });
        return (
          <Link to={link && link} className={classess} key={idx}>
            <Text className="flaticon-dashboard" />
            {label}
          </Link>
        );
      })}
    </React.Fragment>
  );
}

export default ProfileMenu;
