import React from "react";
import { NavLink } from "react-router-dom";

const Link = (props) => {
  return (
    <React.Fragment>
      {props.to ? (
        <NavLink
          className={props.className}
          style={{ ...props.style }}
          to={props.to}
          title={props.title}
        >
          {props.children}
        </NavLink>
      ) : (
        <a
          className={props.className}
          style={{ ...props.style }}
          href={props.href ? props.href : "javascript:void(0)"}
          title={props.title}
          onClick={props.onPress}
        >
          {props.children}
        </a>
      )}
    </React.Fragment>
  );
};

export default Link;
