import React from "react";
import { TextInput, TextArea, Dropdown } from "common";

export const OfferForm = (props) => {
  function getInput(item) {
    switch (item.type) {
      case "textarea":
        return <TextArea {...item} />;
      case "select":
        return (
          <Dropdown {...item} className="browser-default custom-select h50" />
        );

      default:
        return <TextInput {...item} />;
    }
  }
  return (
    <React.Fragment>
      {props.items.map((item, idx) => {
        return (
          <div
            className={item.wrapperClass ? item.wrapperClass : "col-lg-12"}
            key={`OfferForm${idx}`}
          >
            <div className="form-group">
              <label className="col-md-12">{item.label} </label>
              <div className="col-md-12">{getInput(item)}</div>
            </div>
          </div>
        );
      })}
    </React.Fragment>
  );
};

export default OfferForm;
