import React, { useState } from "react";
import { Wrapper, Button, Text, H2 } from "common";

function Accordions({ items }) {
  const [activeIndex, setActiveIndex] = React.useState(null);
  return (
    <React.Fragment>
      {items.map(({ title, description }, idx) => {
        return (
          <Wrapper className="accordion" id="accordionExample">
            <Wrapper className="card">
              <Wrapper
                className={
                  activeIndex == idx ? "card-header active" : "card-header"
                }
              >
                <H2 className="mb-0">
                  <Button
                    className="btn btn-link collapsed"
                    onPress={() => setActiveIndex(idx)}
                  >
                    {title}
                    <Text className="fa fa-plus float-left" />
                  </Button>
                </H2>
              </Wrapper>
              <Wrapper id="collapseOne" className="collapse show">
                {activeIndex == idx && (
                  <Wrapper className="card-body">{description}</Wrapper>
                )}
              </Wrapper>
            </Wrapper>
          </Wrapper>
        );
      })}
    </React.Fragment>
  );
}

export default Accordions;
