import React from "react";
import classNames from "classnames";

export const Button = (props) => {
  return (
    <button
      className={props.className + " button"}
      style={{ ...props.style }}
      onClick={props.onPress}
      type={props.type}
    >
      {props.children}
    </button>
  );
};

export const Text = (props) => {
  function getTextElement(props) {
    switch (props.type) {
      case "paragraph":
        return (
          <p className={props.className} style={{ ...props.style }}>
            {props.title ? props.title : props.children}
          </p>
        );

      default:
        return (
          <span className={props.className} style={{ ...props.style }}>
            {props.title ? props.title : props.children}
          </span>
        );
    }
  }
  return getTextElement(props);
};

export const Icon = (props) => {
  return (
    <i className={props.className} style={{ ...props.style }}>
      {props.children}
    </i>
  );
};

export const SimpleList = (props) => {
  return (
    <li
      className={props.className}
      style={{ ...props.style }}
      onClick={props.onClick}
    >
      {props.children}
    </li>
  );
};

export const RadioToggle = (props) => {
  let parentClasses = classNames({
    "custom-control custom-switch": !props.className,
    [props.className]: props.className,
  });
  let inputClasses = classNames({
    "custom-control-input": !props.inputClass,
    [props.className]: props.className,
  });
  let labelClasses = classNames({
    "custom-control-label": !props.labelClass,
    [props.className]: props.className,
  });
  return (
    <React.Fragment>
      {props.items.map((item, idx) => {
        return (
          <div class={parentClasses} key={`radio${idx}`}>
            <input
              type="radio"
              name={props.name}
              class={inputClasses}
              id={item.id}
            />
            <label class={labelClasses} for={item.for}>
              {item.label}
            </label>
          </div>
        );
      })}
    </React.Fragment>
  );
};
