import React from "react";
import { Wrapper, SimpleList, Link, Text } from "common";
import classNames from "classnames";

function Pagination(props) {
  let disableClass = classNames({
    [`page-item`]: true,
    disabled: props.active < 2,
  });
  var pageCount = [];
  for (var i = 1; i <= props.pageCount; i++) {
    pageCount.push(i);
  }
  if (props.active < props.pageCount) {
    pageCount.splice(props.active + 6, 0, "....");
    pageCount.splice(props.active + 7, 0, props.pageCount);
  }

  return (
    <Wrapper className="mbp_pagination">
      <ul className="page_navigation">
        <SimpleList
          className={disableClass}
          onClick={() => props.active > 0 && props.onClick(props.active - 1)}
        >
          <Link className="page-link" tabindex="-1" aria-disabled="true">
            <Text className="flaticon-left-arrow" /> Previous
          </Link>
        </SimpleList>
        {pageCount
          .slice(
            props.active > 3 ? props.active - 3 : props.active,
            8 + props.active
          )
          .map((number, idx) => {
            var activeClass = classNames({
              [`page-item`]: true,
              active: number == props.active,
            });

            return (
              <SimpleList
                className={activeClass}
                key={idx}
                onClick={() => props.onClick(number)}
              >
                <Link className="page-link">{number}</Link>
              </SimpleList>
            );
          })}

        <SimpleList
          className="page-item"
          onClick={() =>
            props.active < props.pageCount && props.onClick(props.active + 1)
          }
        >
          <Link className="page-link">
            Next <span className="flaticon-right-arrow"></span>
          </Link>
        </SimpleList>
      </ul>
    </Wrapper>
  );
}

export default Pagination;
