import React from "react";
import { Text } from "common";
import classNames from "classnames";

export const List = (props) => {
  let parentClasses = classNames({
    [props.className]: props.className,
  });
  function getItems(items) {
    return (
      <React.Fragment>
        {items &&
          items.length > 0 &&
          items.map((list, idx) => {
            let liClasses = classNames({
              [props.itemsClass]: props.itemsClass,
              [list.class]: list.class,
            });
            return (
              <li
                key={idx}
                className={liClasses}
                style={{ ...props.itemsStyle }}
              >
                {React.isValidElement(list.label) ? (
                  list.label
                ) : (
                  <Text title={list.label} />
                )}
              </li>
            );
          })}
      </React.Fragment>
    );
  }

  switch (props.type) {
    case "ol":
      return (
        <ol className={parentClasses} style={{ ...props.style }} id={props.id}>
          {getItems(props.items)}
        </ol>
      );

    default:
      return (
        <ul className={parentClasses} style={{ ...props.style }} id={props.id}>
          {getItems(props.items)}
        </ul>
      );
  }
};

export default List;
